'use client';

import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';

// Import Custom Hook (Logika)
import { useKategoriPage } from '@/hooks/admin/useKategoriPage';

// Import Komponen UI (Tampilan)
import KategoriFilters from '@/components/admin/kategori/KategoriFilters';
import KategoriTable from '@/components/admin/kategori/KategoriTable';
import KategoriFormDialog from '@/components/admin/kategori/KategoriFormDialog';

export default function KategoriPage() {
    // Panggil logika dari Custom Hook
    const {
        // Data
        kategoris,
        totalData,
        loading,
        
        // Pagination & Filter
        currentPage,
        itemsPerPage,
        searchQuery,
        setSearchQuery,
        setCurrentPage,
        
        // State Modal & Form
        isDialogOpen,
        setIsDialogOpen,
        isSaving,
        editData,
        formData,
        setFormData,
        
        // Fungsi Handler
        handleOpenDialog,
        handleFileChange,
        handleSubmit,
        handleDelete
    } = useKategoriPage();

    return (
        <div className="space-y-6">
            {/* --- 1. HEADER HALAMAN --- */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Data Kategori</h1>
                    <p className="text-gray-500 text-sm">Kelola kategori produk untuk POS dan Inventaris.</p>
                </div>
                
                <Button 
                    className="bg-[#D93F21] hover:bg-[#b9351b] shadow-sm"
                    onClick={() => handleOpenDialog()}
                >
                    <Plus className="w-4 h-4 mr-2" /> Tambah Kategori
                </Button>
            </div>

            {/* --- 2. FILTER PENCARIAN --- */}
            <KategoriFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
            />

            {/* --- 3. TABEL DATA --- */}
            <KategoriTable 
                loading={loading}
                data={kategoris}
                totalData={totalData}
                currentPage={currentPage}
                itemsPerPage={itemsPerPage}
                onPageChange={setCurrentPage}
                onEdit={handleOpenDialog}
                onDelete={handleDelete}
            />

            {/* --- 4. FORM DIALOG (CREATE / EDIT) --- */}
            <KategoriFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                isSaving={isSaving}
                formData={formData}
                setFormData={setFormData}
                initialData={editData}
                onFileChange={handleFileChange}
            />
        </div>
    );
}