'use client';

import { useState, useEffect, ChangeEvent } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { Kategori } from '@/types';

export function useKategoriPage() {
    // State Data
    const [kategoris, setKategoris] = useState<Kategori[]>([]);
    const [loading, setLoading] = useState(true);
    
    // Filter & Pagination
    const [searchQuery, setSearchQuery] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10);

    // Modal & Form State
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [isSaving, setIsSaving] = useState(false);
    const [editData, setEditData] = useState<Kategori | null>(null);
    const [formData, setFormData] = useState({
        nama_kategori: '',
        deskripsi: '',
        icon: null as File | null,
    });

    // 1. Fetch Data
    const fetchKategoris = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/kategoris');
            setKategoris(response.data);
        } catch (error) {
            console.error("Error fetching data:", error);
            toast.error("Gagal memuat data kategori.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchKategoris();
    }, []);

    // 2. Filter Logic
    const filteredKategoris = kategoris.filter((item) => 
        item.nama_kategori.toLowerCase().includes(searchQuery.toLowerCase())
    );

    // Pagination Logic
    useEffect(() => { setCurrentPage(1); }, [searchQuery]);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredKategoris.slice(indexOfFirstItem, indexOfLastItem);

    // 3. Handlers
    const resetForm = () => {
        setFormData({ nama_kategori: '', deskripsi: '', icon: null });
        setEditData(null);
        setIsDialogOpen(false);
    };

    const handleOpenDialog = (kategori?: Kategori) => {
        if (kategori) {
            setEditData(kategori);
            setFormData({
                nama_kategori: kategori.nama_kategori,
                deskripsi: kategori.deskripsi || '',
                icon: null
            });
        } else {
            resetForm();
        }
        setIsDialogOpen(true);
    };

    const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            setFormData(prev => ({ ...prev, icon: e.target.files![0] }));
        }
    };

    const handleSubmit = async () => {
        setIsSaving(true);
        try {
            if (editData) {
                // UPDATE
                await axios.put(`/api/kategoris/${editData.id}`, {
                    nama_kategori: formData.nama_kategori,
                    deskripsi: formData.deskripsi
                });
                
                if (formData.icon) {
                    const imgData = new FormData();
                    imgData.append('icon', formData.icon);
                    await axios.post(`/api/kategoris/${editData.id}/icon`, imgData, {
                        headers: { 'Content-Type': 'multipart/form-data' },
                    });
                }
                toast.success("Kategori berhasil diperbarui!");
            } else {
                // CREATE
                const data = new FormData();
                data.append('nama_kategori', formData.nama_kategori);
                data.append('deskripsi', formData.deskripsi);
                if (formData.icon) {
                    data.append('icon', formData.icon);
                }

                await axios.post('/api/kategoris', data, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                });
                toast.success("Kategori berhasil ditambahkan!");
            }
            resetForm();
            fetchKategoris();
        } catch (error: any) {
            console.error(error);
            const pesan = error.response?.data?.message || "Gagal menyimpan data.";
            if (error.response?.data?.errors?.icon) {
                toast.error(error.response.data.errors.icon[0]);
            } else {
                toast.error(pesan);
            }
        } finally {
            setIsSaving(false);
        }
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Yakin ingin menghapus kategori ini?")) return;
        try {
            await axios.delete(`/api/kategoris/${id}`);
            toast.success("Kategori berhasil dihapus");
            fetchKategoris();
        } catch (error) {
            toast.error("Gagal menghapus kategori");
        }
    };

    return {
        // Data & State
        kategoris: currentItems,
        totalData: filteredKategoris.length,
        loading,
        currentPage,
        itemsPerPage,
        searchQuery,
        isDialogOpen,
        isSaving,
        editData,
        formData,

        // Setters
        setSearchQuery,
        setCurrentPage,
        setFormData,
        setIsDialogOpen,

        // Functions
        handleOpenDialog,
        handleFileChange,
        handleSubmit,
        handleDelete,
        resetForm
    };
}