'use client';

import Image from 'next/image';
import { Pencil, Trash2, Loader2, Search, ChevronLeft, ChevronRight, Wallet, Tags } from 'lucide-react';
import { Button } from '@/components/ui/button';
import {
    Table, TableBody, TableCell, TableHead, TableHeader, TableRow,
} from '@/components/ui/table';
import { Card } from '@/components/ui/card';
import { Kategori } from '@/types';

interface KategoriTableProps {
    loading: boolean;
    data: Kategori[];
    totalData: number;
    currentPage: number;
    itemsPerPage: number;
    onPageChange: (page: number) => void;
    onEdit: (item: Kategori) => void;
    onDelete: (id: number) => void;
}

export default function KategoriTable({
    loading,
    data,
    totalData,
    currentPage,
    itemsPerPage,
    onPageChange,
    onEdit,
    onDelete
}: KategoriTableProps) {
    
    const totalPages = Math.ceil(totalData / itemsPerPage);
    const startNumber = (currentPage - 1) * itemsPerPage + 1;

    return (
        <Card className="overflow-hidden border shadow-sm">
            <div className="rounded-md border-b">
                <Table>
                    <TableHeader>
                        <TableRow className="bg-gray-50">
                            <TableHead className="w-[50px] text-center">No</TableHead>
                            <TableHead className="w-[80px]">Ikon</TableHead>
                            <TableHead>Nama Kategori</TableHead>
                            <TableHead className="hidden md:table-cell">Deskripsi</TableHead>
                            <TableHead className="text-right">Aksi</TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody>
                        {loading ? (
                            <TableRow>
                                <TableCell colSpan={5} className="h-24 text-center text-gray-500">
                                    <div className="flex justify-center items-center gap-2">
                                        <Loader2 className="h-5 w-5 animate-spin" /> Memuat data...
                                    </div>
                                </TableCell>
                            </TableRow>
                        ) : data.length === 0 ? (
                            <TableRow>
                                <TableCell colSpan={5} className="h-32 text-center text-gray-500">
                                    <div className="flex flex-col items-center justify-center">
                                        <Search className="h-8 w-8 text-gray-300 mb-2" />
                                        <p>Belum ada data kategori.</p>
                                    </div>
                                </TableCell>
                            </TableRow>
                        ) : (
                            data.map((item, index) => (
                                <TableRow key={item.id} className="hover:bg-gray-50">
                                    <TableCell className="text-center text-gray-500 text-sm">
                                        {startNumber + index}
                                    </TableCell>
                                    <TableCell>
                                        <div className="relative h-10 w-10 rounded-lg overflow-hidden bg-gray-100 border border-gray-200 flex items-center justify-center">
                                            {item.icon_url ? (
                                                <Image 
                                                    src={item.icon_url} 
                                                    alt={item.nama_kategori} 
                                                    fill 
                                                    className="object-cover" 
                                                    unoptimized 
                                                    onError={(e) => {
                                                        (e.target as HTMLElement).style.display = 'none';
                                                        (e.target as HTMLElement).parentElement!.classList.add('bg-gray-200');
                                                    }} 
                                                />
                                            ) : (
                                                <Tags className="w-5 h-5 text-gray-400" />
                                            )}
                                        </div>
                                    </TableCell>
                                    <TableCell className="font-medium text-gray-900">
                                        {item.nama_kategori}
                                    </TableCell>
                                    <TableCell className="hidden md:table-cell text-gray-500 text-sm truncate max-w-[200px]">
                                        {item.deskripsi || '-'}
                                    </TableCell>
                                    <TableCell className="text-right space-x-1">
                                        <Button variant="ghost" size="icon" onClick={() => onEdit(item)} className="h-8 w-8 text-blue-600 hover:text-blue-700 hover:bg-blue-50">
                                            <Pencil className="h-4 w-4" />
                                        </Button>
                                        <Button variant="ghost" size="icon" onClick={() => onDelete(item.id)} className="h-8 w-8 text-red-600 hover:text-red-700 hover:bg-red-50">
                                            <Trash2 className="h-4 w-4" />
                                        </Button>
                                    </TableCell>
                                </TableRow>
                            ))
                        )}
                    </TableBody>
                </Table>
            </div>
            
            {!loading && totalData > 0 && (
                <div className="flex items-center justify-between p-4 bg-gray-50/50">
                    <div className="text-sm text-gray-500">
                         Total: <strong>{totalData}</strong> Kategori
                    </div>
                    <div className="flex items-center space-x-2">
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage - 1)} disabled={currentPage === 1} className="h-8 w-8 p-0">
                            <ChevronLeft className="h-4 w-4" />
                        </Button>
                        <span className="text-sm font-medium px-2">Hal {currentPage} / {totalPages}</span>
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage + 1)} disabled={currentPage === totalPages} className="h-8 w-8 p-0">
                            <ChevronRight className="h-4 w-4" />
                        </Button>
                    </div>
                </div>
            )}
        </Card>
    );
}