'use client';

import { FormEvent, ChangeEvent } from 'react';
import { Loader2, UploadCloud } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import {
    Dialog, DialogContent, DialogFooter, DialogHeader, DialogTitle, DialogDescription,
} from '@/components/ui/dialog';
import { Kategori } from '@/types';

interface KategoriFormDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    onSubmit: () => void;
    isSaving: boolean;
    formData: {
        nama_kategori: string;
        deskripsi: string;
        icon: File | null;
    };
    setFormData: (data: any) => void;
    initialData: Kategori | null;
    onFileChange: (e: ChangeEvent<HTMLInputElement>) => void;
}

export default function KategoriFormDialog({
    isOpen,
    onOpenChange,
    onSubmit,
    isSaving,
    formData,
    setFormData,
    initialData,
    onFileChange
}: KategoriFormDialogProps) {
    
    const handleSubmit = (e: FormEvent) => {
        e.preventDefault();
        onSubmit();
    };

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-[500px]">
                <DialogHeader>
                    <DialogTitle>{initialData ? 'Edit Kategori' : 'Tambah Kategori Baru'}</DialogTitle>
                    <DialogDescription>
                        {initialData ? 'Perbarui informasi kategori di bawah ini.' : 'Masukkan detail kategori produk baru.'}
                    </DialogDescription>
                </DialogHeader>

                <form onSubmit={handleSubmit} className="space-y-5 mt-2">
                    <div className="space-y-2">
                        <Label htmlFor="nama">Nama Kategori <span className="text-red-500">*</span></Label>
                        <Input 
                            id="nama" 
                            placeholder="Contoh: Makanan Berat" 
                            value={formData.nama_kategori}
                            // FIX: Tambahkan tipe (prev: any)
                            onChange={(e) => setFormData((prev: any) => ({...prev, nama_kategori: e.target.value}))}
                            required
                        />
                    </div>
                    
                    <div className="space-y-2">
                        <Label htmlFor="deskripsi">Deskripsi</Label>
                        <Textarea 
                            id="deskripsi" 
                            placeholder="Keterangan singkat..." 
                            value={formData.deskripsi}
                            // FIX: Tambahkan tipe (prev: any)
                            onChange={(e) => setFormData((prev: any) => ({...prev, deskripsi: e.target.value}))}
                            className="resize-none h-24"
                        />
                    </div>
                    
                    <div className="space-y-2">
                        <Label htmlFor="icon">Ikon Kategori</Label>
                        <div className="border-2 border-dashed border-gray-200 rounded-lg p-4 hover:bg-gray-50 transition-colors text-center cursor-pointer relative">
                            <input 
                                id="icon" 
                                type="file" 
                                accept="image/*"
                                onChange={onFileChange}
                                className="absolute inset-0 w-full h-full opacity-0 cursor-pointer"
                            />
                            <div className="flex flex-col items-center justify-center gap-1 text-sm text-gray-500">
                                <UploadCloud className="w-8 h-8 text-gray-400 mb-1" />
                                <span className="font-medium text-gray-700">Klik untuk upload gambar</span>
                                <span className="text-xs text-gray-400">JPG, PNG (Max 2MB)</span>
                                {formData.icon && (
                                    <span className="text-xs text-green-600 font-bold mt-1 bg-green-50 px-2 py-0.5 rounded">
                                        File terpilih: {formData.icon.name}
                                    </span>
                                )}
                            </div>
                        </div>
                    </div>

                    <DialogFooter className="pt-2">
                        <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>
                            Batal
                        </Button>
                        <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                            {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />} 
                            {initialData ? 'Perbarui' : 'Simpan'}
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}